*{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* app css styling */
.app__side {
  display: flex;
  height: 100vh;
}

.app__bodyFlex {
  flex: 1;
  flex-direction: column;
}


/* Navbar css styling */
.navbar {
  flex: 0.;
  color: white;
  background-color: black;
  display: block;
  align-items: center;  
}

a{
  display: flex;
  flex-direction: column;
  color: white;
  padding: 10px;
  text-decoration: none;
}

.links{
  display: flex;
  flex-direction: column;
  margin-top: 8rem;
  align-items: center;
}

.links li{
  list-style: none;
  margin: 15px 0 15px 5px;
  align-items: center;
}

/* Header css styling */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: black;
  padding: 15px 10px;
  cursor: pointer;
}

.header-logo{
  font-weight: bold;
  font-size: 20px;
  color: white;
  margin-left: 20px;
}

span{
  font-size: 32px;
}

.search{
  display: flex;
  align-items: center;
  width: 300px;
  outline: none;
  position: relative;
  background-color: gray;
  border-radius: 8px;
  justify-content: center;
  border: none;
}

.search:hover{
  cursor: pointer;
}

.header-form-input{
  width: 85%;
  padding: 10px;
  border: none;
  outline: none;
  background: transparent;
  /* margin-right: 15px; */
  font-style: normal;
  font-weight: normal;
  font-size: 0.8125rem;
  line-height: 15px;
  color: #000;
}

.header-form-input::placeholder {
  font-style: normal;
  font-weight: normal;
  font-size: 0.9125rem;
  line-height: 15px;
  color: white;
}

button{
  background: gray;
  outline: none;
  border: none;
  cursor: pointer;
}

svg{
  color: black;
  font-size: 1.3rem;
  background: transparent;
  outline: none;
  border: none;
}

@media screen and (max-width: 768px){
  .header-logo{
    margin-left: 0px;
  }
  
  span{
    font-size: 20px;
  }

  .search{
    width: 200px;
  }
}

@media screen and (max-width: 400px) {
  .header-logo{
    font-size: 15px;
  }

  .search{
    width: 200px;
  }

  .header-logo{
    margin-left: 1px;
  }
}

/* Home css styling */
.home h1{
  text-align: center;
  margin: 10px 0;
}

.blog-prev{
  width: 350px;
  padding: 10px 16px;
  margin: 10px 20px;
  border-bottom: 1px solid #e07575;
}

.blog-prev:hover{
  box-shadow: 8px 8px 5px rgba(0, 0, 0, 0.4);
}

.blog-prev h2{
  font-size: 20px;
  margin-bottom: 8px;
  color: #707890;
}

/* About css styling */
.about h2{
  text-align: center;
  margin-top: 50px;
}

.time{
  margin: 20px 0;
  text-align: center;
  font-family: sans-serif;
}

.btn1{
  background-color: green;
  margin: 20px 10px;
  padding: 8px 12px;
  border-radius: 10px;
}

.btn2{
  background-color: red;
  margin: 20px 10px;
  padding: 8px 12px;
  border-radius: 10px;
}

/* Article css styling */
.article h2{
  text-align: center;
  margin: 50px 0;
}

.text{
  margin: 20px 0;
  text-align: center;
}

.text p{
  margin: 10px;
}